

.searchbar {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1100;
    min-height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color: #3F3B6C;
}

.button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.button:hover {
    opacity: 0.7;
}

.buttonLabel {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
}

.input {
    display: inline-block;
    max-width: 500px;
    height: 46px;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
}

.input::placeholder {
    font: inherit;
    font-size: 18px;
}