

.button {
    color: white;
    margin: 0 auto;
    min-width: 150px;
    display: inline-block;
    padding: 10px;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    background-color: #3F3B6C;
    text-align: center;
    border-radius: 8px;
    border: 0;
    cursor: pointer;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button:hover,
.button:focus {
    background-color: #464276;
}