.item {
    overflow: hidden;
    border-radius: 4px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.image {
    width: 100%;
    height: 260px;
    display: block;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.5, 0, 0.3, 1);
}

.image:hover {
    transform: scale(1.06);
}